import React from 'react';
import Slider from 'react-slick';
import Sofa1 from '../../images/gallery-6.jpg';
import Sofa2 from '../../images/gallery-7.jpg';
import Sofa3 from '../../images/sofa-5.jpg';
import Sofa4 from '../../images/gallery-11.jpg';
import Sofa5 from '../../images/sofa-7.jpg';
import {ZigZagSliderConfig} from '../../config';
import TrackVisibility from 'react-on-screen';

class Work extends React.Component {
  constructor(props) {
    super(props);
    this.play = this.play.bind(this);
    this.pause = this.pause.bind(this);
    this.handleSlide = this.handleSlide.bind(this);
  }

  play() {
    this.slider.slickPlay();
  }

  pause() {
    this.slider.slickPause();
  }

  handleSlide(prop) {
    if (prop) {
      this.timeOut = setTimeout(() => {
        this.play();
      }, 1000);
    }
    else {
      // remove cached timeout if found
      if (this.timeOut) {
        clearTimeout(this.timeOut);
        this.timeOut = null;
      }
      // give slider time to initialized and then call pause method
      setTimeout(() => {
        this.pause();
      });
    }
  }

  render() {
    const SofaImages = [Sofa1, Sofa2, Sofa3, Sofa4, Sofa5];
    return (
      <div className="zig-zag-section sofa-section">
        <h4>Our Work</h4>
        <div className="grid-flex">
          <div className="col-sm-6 sofa-image-col">
            <TrackVisibility className="track-visibility">
              {({ isVisible }) => (
                <Slider
                  onInit={this.handleSlide(isVisible)}
                  ref={(slider) => { this.slider = slider; }}
                  {...ZigZagSliderConfig}
                  className="the-slick-slider"
                >
                  {/* sofaImage sofa contains all Images of sofas */}
                  {SofaImages.map((image, index) => (
                    <div className="the-slick-slider-div" key={parseInt(index.toString(), 10)}>
                      <div
                        className="sofa-image"
                        style={{ backgroundImage: `url(${image})` }}
                      />
                    </div>
                  ))}
                </Slider>
              )
              }
            </TrackVisibility>
          </div>
          <div className="col-sm-6 sofa-content-col">
            <ObjectTextContent />
          </div>
        </div>
      </div>
    );
  }
}

function ObjectTextContent() {
  return (
    <div className="aligner-item">
      <h5>Our expertise</h5>
      <h3>Sofa Work</h3>
      <p>
        We make sofa as per customer requirements.
        We made sofa like Regular Sofas, Lounger, Rajwadi Sofas, Sofa cum Bed, L-Corner Sofas, Rounded Sofas, etc.
      </p>
    </div>
  );
}

export default Work;