import React from 'react';
import Slider from 'react-slick';
import {ZigZagSliderConfig} from '../../config';
import TrackVisibility from 'react-on-screen';

class HomeProduct extends React.Component {
  constructor(props) {
    super(props);
    this.play = this.play.bind(this);
    this.pause = this.pause.bind(this);
    this.handleSlide = this.handleSlide.bind(this);
  }

  play() {
    this.slider.slickPlay();
  }

  pause() {
    this.slider.slickPause();
  }

  handleSlide(prop) {
    if (prop) {
      this.timeOut = setTimeout(() => {
        this.play();
      }, 1000);
    }
    else {
      // remove cached timeout if found
      if (this.timeOut) {
        clearTimeout(this.timeOut);
        this.timeOut = null;
      }
      // give slider time to initialized and then call pause method
      setTimeout(() => {
        this.pause();
      });
    }
  }

  render() {
    const HomeProducts = ['https://www.sleepwellproducts.com/media/product/COMPR-W-02-2018-001-500x500-min.jpg', 
    'https://www.sleepwellproducts.com/media/product/detail_curves.jpg', 
    'https://www.sleepwellproducts.com/media/product/xperia-high-rise.jpg', 
    'https://www.sleepwellproducts.com/media/product/detail_shield2.jpg', 
    'https://www.sleepwellproducts.com/media/product/detail_backcushions.jpg'];
    return (
      <div className="zig-zag-section home-product-section">
        <div className="grid-flex">
          <div className="col-sm-6 home-product-image-col">
            <TrackVisibility className="track-visibility">
              {({ isVisible }) => (
                <Slider
                  onInit={this.handleSlide(isVisible)}
                  ref={(slider) => { this.slider = slider; }}
                  {...ZigZagSliderConfig}
                  className="the-slick-slider"
                >
                  {/* HomeProducts Object contains all Images of home-products */}
                  {HomeProducts.map((image, index) => (
                    <div className="the-slick-slider-div" key={parseInt(index.toString(), 10)}>
                      <div
                        className="home-product-image"
                        style={{ backgroundImage: `url(${image})`, backgroundSize: 'cover' }}
                      />
                    </div>
                  ))}
                </Slider>
              )
              }
            </TrackVisibility>
          </div>
          <div className="col-sm-6 home-product-content-col">
            <div className="aligner-item">
              <h5>Sleepwell Home Product Range</h5>
              <h3>Home Products</h3>
              <p>
                We have an exclusive range of Sleepwell Home Product like
                Comforters, Premium Pillow, My Pillows, Fiber Pillows, Mattress Protector, Blankets, Flexi - PUF Pillows, etc.
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default HomeProduct;