import React from 'react';
import Slider from 'react-slick';
import {ZigZagSliderConfig} from '../../config';
import TrackVisibility from 'react-on-screen';

class BedSection extends React.Component {
  constructor(props) {
    super(props);
    this.play = this.play.bind(this);
    this.pause = this.pause.bind(this);
    this.handleSlide = this.handleSlide.bind(this);
  }

  play() {
    this.slider.slickPlay();
  }

  pause() {
    this.slider.slickPause();
  }

  handleSlide(prop) {
    if (prop) {
      this.timeOut = setTimeout(() => {
        this.play();
      }, 1000);
    }
    else {
      // remove cached timeout if found
      if (this.timeOut) {
        clearTimeout(this.timeOut);
        this.timeOut = null;
      }
      // give slider time to initialized and then call pause method
      setTimeout(() => {
        this.pause();
      });
    }
  }

  render() {
    const BedImages = ['https://www.sleepwellproducts.com/media/product/series-4_0-medium2.jpg', 
    'https://www.sleepwellproducts.com/media/product/detail_inspire_supportec.jpg', 
    'https://www.sleepwellproducts.com/media/product/detail_admire_supportec.jpg', 
    'https://www.sleepwellproducts.com/media/product/detail_nexa1.jpg', 
    'https://www.sleepwellproducts.com/media/product/detail_esteem_supportec.jpg'];
    return (
      <div className="zig-zag-section bed-section">
        <h4>Exclusive Delar of SleepWell Mattresses</h4>
        <div className="grid-flex">
          <div className="col-sm-6 bed-image-col">
            <TrackVisibility className="track-visibility">
              {({ isVisible }) => (
                <Slider
                  onInit={this.handleSlide(isVisible)}
                  ref={(slider) => { this.slider = slider; }}
                  {...ZigZagSliderConfig}
                  className="the-slick-slider"
                >
                  {BedImages.map((image, index) => (
                    <div className="the-slick-slider-div" key={parseInt(index.toString(), 10)}>
                      <div
                        className="bed-image"
                        style={{ backgroundImage: `url(${image})` }}
                      />
                    </div>
                  ))}
                </Slider>
              )
              }
            </TrackVisibility>
          </div>
          <div className="col-sm-6 bed-content-col">
            <ObjectTextContent />
          </div>
        </div>
      </div>
    );
  }
}

function ObjectTextContent() {
  return (
    <div className="aligner-item">
      <h5>Sleepwell Bed Range</h5>
      <h3>Feel the Comfort</h3>
      <p>
        All type of Sleepwell mattresses are available. We have exclusive range of Comfort Cell Range, My Matteress Range, Technology Product, Back Support Range.
      </p>
    </div>
  );
}

export default BedSection;